.posts{
        .title{
            color: #47525E;
            font-family: Lato;
            font-size: 35px;
            font-weight: 400;
            line-height: 45px;
            width: 244.5px;
            text-align: left;
            margin-bottom: 15px;
            margin-top: 15px;
        }
        form{
            textarea{
                padding: 10px 10px;
                background-color: #E6EEEA;
                width: 1004px;
                height: 84px;
            }
            .btn{
                background-color: #0CB658;
