.dialogs{
    .title{
            color: #47525E;
            font-size: 26px;
            font-weight: 400;
            text-align: left;
            text-transform: uppercase;
    }
    .block{
        display:grid;
        grid-template-columns: 2fr 1fr 9fr;
        .names{
            display: flex;
            flex-direction: column;
            position: relative;
           div{
               margin-top: 30px;
               .dot{
                position: absolute;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                background-color: #8190A5;
                margin-top: 8px;
                left: 10px;
                }
           }
            a{
                color: #8190A5;
                font-size: 18px;
                font-weight: 400;
                text-align: left;
                text-decoration-line: none;
                margin:40px 0 0 30px;
                &:hover{
                    cursor: pointer;
                }
            }
            a.active{
                color: #343F4B;
                font-size: 24px;
                font-weight: 400;
                text-align: left;
                &:hover{
                    cursor: pointer;
                }
            }
            
        }
        .line{
            width: 1px;
            height: 100%;
            background-color: #7E55F3;
            margin-top: 10px;
        }
    }
}
.message_item{
    color: #8190A5;
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    text-align: center;
    padding: 18px 18px;
    background-color: #FFFFFF;
    border: 2px solid #C0CCDA;
    max-width: 200px;
    border-radius: 10px;
    margin-left: 100px;
}
.message{
    position: relative;
}
.message_block{
    margin-top: 50px;
    .smile{
        position: absolute;
        background-color: #E5E9F2;
        width: 70px;
        height: 70px;
        border-radius: 50%;
        text-align: center;
    
        img{
            display: inline-block;
            width: 40px;
            position: absolute;
            transform: translate(-50%,-50%);
            top: 36px;
        }
    }
}

.send_message{
    position: relative;
    .textarea{
        position: absolute;
        margin-top: 40px;
        padding: 10px 10px;
        background-color: #E6EEEA;
        width: 90%;
        height: 84px;
        right: 0;
    }
    
    .btn{
        display: block;
        position: absolute;
        background-color: #0CB658;
        width: 162px;
        height: 36px;
        color: #FFFFFF;
        font-family: Lato;
        font-size: 26px;
        font-weight: 400;
        line-height: 33px;
        text-align: left;
        border: none;
        margin-top: 10px;
        position: absolute;
        right: 0;
        top: 164px;
    }
}