.post{
    img{
        display: block;
        width: 50px;  
        height: 50px;
        margin-top: 70px;
        border-radius:50%;
        margin-bottom: 10px;
    }
    .name{
        color: black;
        font-size: 22px;
        margin-bottom: 10px;
        font-weight: 900;
    }
    .descr{
        color: #47525E;
        font-family: Lato;
        font-size: 20px;
        font-weight: 400;
        line-height: 33px;
        text-align: left;
    }
    &:last-child{
        hr{
            display: none;
        }
    }
}