.title{
    color: #47525E;
    font-family: Lato;
    font-size: 26px;
    font-weight: 400;
    line-height: 33px;
    text-align: left;
    margin-bottom: 30px;
}
.btn {
    display: block;
    background-color: #13CE66;
    width: auto;
    height: 69px;
    color: #FFFFFF;
    font-family: Lato;
    font-size: 30px;
    font-weight: 700;
    line-height: 36px;
    text-align: center;
    border: none;
    padding: 17px 54px 17px 54px;
    margin: 0 auto; 
    margin-top: 90px;
    &:hover{
        cursor: pointer;
    }
}
.page{
    width: 10px;
    height: 10px;
    margin: 5px 5px;
    padding: 3px 3px;
    color:#fff;
    background-color: #13CE66;
    &:hover{
        cursor: pointer;
    }
}
.page_active{
    color: black;
}
.user_item{
    position: relative;
    display: flex;
    justify-content: flex-start;
    margin-top: 50px;
    .img_box{
        width: 90px;
        height: 90px;
        background-color: #E5E9F2;
        border-radius: 100%;
        margin-right: 30px;
        margin-left: 30px;
        img{
            width: 90px;
            height: 90px;
            border-radius: 100%;
        }
    }
    .follow{
        position: absolute; 
        background-color: #13CE66;
        width: 100px;
        height: 31px;
        color: #FFFFFF;
        font-family: Lato;
        font-size: 26px;
        font-weight: 400;
        line-height: 33px;
        text-align: center;
        border:none;
        top: 100px;
        left: 24px;
        &:hover{
            cursor: pointer;
        }
    }
    .descr_block{
        display: flex;
        width: 550;
        justify-content: space-between;
        margin-left: 25px;
        border: 2px solid #8190A5;
        border-radius: 8px;
        padding: 30px;
        .left_block{
            .name{
                color: #8190A5;
                font-family: Lato;
                font-size: 21px;
                font-weight: 700;
                line-height: 36px;
                width: 152px;
                text-align: left;
            }
            .about_me{
                color: #8190A5;
                font-family: Lato;
                font-size: 18px;
                font-weight: 400;
                line-height: 30px;
                width: 585px;
                text-align: left;
                margin-top: 20px;
            }
        }
        .right_block{
            .location{
                color: #8190A5;
                font-family: Lato;
                font-size: 21px;
                font-weight: 700;
                line-height: 36px;
                width: 152px;
                text-align: right;
            }
        }

    }
}