.menu{
    background-color: #13CE66;
    width: 264px;
    height: 678px;
    display: flex;
    flex-direction: column;
    grid-area: menu;
    padding: 5px 5px;
    a{
        color: #fff;
        text-decoration-line: none;
        margin: 10px 5px;
        color: #FFFFFF;
        font-family: Lato;
        font-size: 30px;
        font-weight: 400;
        line-height: 44px;
        width: 227px;
        text-align: left;
    }
    .active{
        color: black;
    }

}

a.friends{
    margin-top: 50px;
    color: #FFFFFF;
    font-family: Lato;
    font-size: 50px;
    font-weight: 300;
    line-height: 65px;
    text-align: left;
}
.friends_icons{
    display: flex;
    justify-content: space-between;
    
}
